const allWords ={};
const totalContext = require.context('./', true, /\.js$/);
totalContext
  .keys()
  .filter(key=>key!=="./index.js")
  .forEach(key => {

    // .js
    let path = key.slice(0,-3).split('/')
    let temp = allWords;

    path.forEach((name,index)=>{
      //  ./
      if(index===0){
        return
      }

      if(index===path.length-1){
        temp[name]= totalContext(key).default
      }else{
        // jsjs
        if(!temp[name]){
          temp[name]={}
        }
        temp = temp[name]
      }
    })
  });


export default allWords



