export const getUserInfo =({id})=>{
  const data = {
    id
  };
  return axios.request({
    url: '/userinfo/'+id,
    params: data,
    method: 'get'
  })
};

/**
 * 
 * @param userName 
 * @param fullName 
 * @param passwd 
 * @param phoneNumber 
 * @param orgId id
 * @param roleId id
 * @param allData 
 * @param state  1- 0- 2-
 * @returns {*}
 */
export const addUser = ({phoneNumber, userName, fullName, passwd, orgId, allData,state, roleId}) => {
  const data = {
    phoneNumber,
    passwd,
    userName,
    orgId,
    state,
    fullName,
    roleId,
    allData
  };
  return axios.request({
    url: '/userinfo/addUser',
    data,
    method: 'post'
  })
};

/**
 *
 * @param userId
 * @returns {*}
 */
export const deleteUser = ({userId}) => {
  const data = {
    userId
  };
  return axios.request({
    url: '/userinfo/delUser',
    data,
    method: 'post'
  })
};

/**
 * 
 * @param userId  id
 * @param userName 
 * @param fullName 
 * @param passwd 
 * @param phoneNumber 
 * @param orgId id
 * @param roleId id
 * @param allData 
 * @param state  1- 0- 2-
 * @returns {*}
 */
export const editUser = ({state,userId,phoneNumber, userName, fullName, passwd, orgId, allData, roleId}) => {
  const data = {
    state,
    userId,
    phoneNumber,
    passwd,
    userName,
    orgId,
    fullName,
    roleId,
    allData
  };
  return axios.request({
    url: '/userinfo/editUser',
    data,
    method: 'post'
  })
};

