import buildConfigs from '../../config'

let buildConfig
if (process.env.NODE_ENV !== 'production') {
  buildConfig = buildConfigs.dev
} else {
  buildConfig = buildConfigs.build
}

export default {
  /**
   * @description tokenCookie1
   */
  cookieExpires: 1,

  /**
   * api 
   */
  baseUrl: '/device-ai',

  downLoadUrl: '/device-batch-ai',

  suanpanUrl: '/suanpan',

  faultUrl: '/fault',

  dpfUrl: '/dpf',

  decisionUrl: '/decision',
  /**
   * 
   */
  useMock: false,

  buildConfig
}



